// src/components/AttendanceTable.tsx
"use client";

import React from "react";
import { useAttendanceStore } from "@/store/attendanceStore";

export default function AttendanceTable() {
  const rows = useAttendanceStore((s) => s.records);

  if (!rows || rows.length === 0) {
    return <div className="p-4 text-sm text-muted">No attendance uploaded yet.</div>;
  }

  // derive columns from first row
  const columns = Object.keys(rows[0]);

  return (
    <div className="overflow-x-auto bg-white rounded shadow">
      <table className="table w-full min-w-[700px]">
        <thead>
          <tr>
            {columns.map((c) => (
              <th key={c} className="text-left">{c}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {rows.map((r, idx) => (
            <tr key={idx} className="hover">
              {columns.map((c) => (
                <td key={c + idx} className="py-2">
                  {String((r as any)[c] ?? "")}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
