// src/components/UploadAttendance.tsx
"use client";

import React, { useState } from "react";
import { parseAttendanceFile } from "@/lib/excelParser";
import { useAttendanceStore } from "@/store/attendanceStore";

export default function UploadAttendance() {
  const setRecords = useAttendanceStore((s) => s.setRecords);
  const [fileName, setFileName] = useState<string | null>(null);

  async function onChange(e: React.ChangeEvent<HTMLInputElement>) {
    const file = e.target.files?.[0];
    if (!file) return;
    setFileName(file.name);

    try {
      const rows = await parseAttendanceFile(file);
      setRecords(rows);
      // optional: send to API for persistence
      // await fetch('/api/attendance/upload', { method: 'POST', body: JSON.stringify(rows), headers: {'Content-Type':'application/json'} })
      alert(`Uploaded ${rows.length} rows`);
    } catch (err) {
      console.error(err);
      alert("Failed to parse file. Make sure it's a valid Excel/CSV.");
    }
  }

  return (
    <div className="flex flex-col gap-2">
      <label className="inline-block">
        <span className="btn btn-primary cursor-pointer">Upload Attendance</span>
        <input
          onChange={onChange}
          accept=".xlsx,.xls,.csv"
          type="file"
          className="hidden"
        />
      </label>
      {fileName && <div className="text-sm text-muted">Uploaded: {fileName}</div>}
    </div>
  );
}
